import React from "react";
import { Map, GoogleApiWrapper, Marker } from "google-maps-react";

class App extends React.Component {
  render() {
    const mapStyles = {
      width: "100%",
      height: "100%"
    };

    return (
      <Map
        google={this.props.google}
        zoom={8}
        style={mapStyles}
        defaultCenter={{ lat: this.props.lot, lng: this.props.lon }}
      >
        <Marker position={{ lat: this.props.lot, lng: this.props.lon }} />
      </Map>
    );
  }
}
